<template>
  <div>
    <div id="header">
      <header-nav></header-nav>
    </div>
    <div class="clearfix content padding-top-100" style="min-height: 800px">
      <!--  最热-->
      <el-tabs class="mt20" v-model="activeName" @tab-click="handleClick">
        <el-tab-pane v-for="(item, index) in editableTabs"
                     :key="item.name"
                     :label="item.title"
                     :name="item.name">
          <ul class="theme-list clearfix">
            <li class="item" v-for="(item,index) in themeList" :key="index">
              <div class="cover br5 bg-img xl" @click="navToDetail(item.topicid)"
                   :style="{ backgroundImage: 'url(' + osshost + item.topicimg +detailpic+ ')' }">
              </div>
              <div class="bg-sd">
                <div class="name text-tips">{{ item.title }}</div>
                <div class="author">
                  <img :src="osshost + item.faceimg + userface" class="headicon br50" @click="navToIndex(item.userid)">
                  <span class="nickname text-lips" style="max-width: 150px">{{ item.nickname }}</span>
                  <span class="right text-xm el-icon-star-off margin-right-xs block">{{ item.collections }}</span>
                </div>
              </div>
            </li>
          </ul>
          <infinite-loading @infinite="infiniteHandler" class="mt50 mb60">
            <div slot="spinner"><i class="el-icon-loading"></i>小弟拼命加载中...</div>//加载中的文字
            <div slot="no-more" class="el-icon-food grayColor">已加载完毕!</div>//加载完毕的文字
            <div slot="no-results" class="el-icon-coffee-cup grayColor">暂无数据:(</div>//没有数据的文字
          </infinite-loading>
        </el-tab-pane>
      </el-tabs>
    </div>
    <FooterBottom></FooterBottom>
    <Totop></Totop>
  </div>
</template>

<script>
  import ThemeItem from "@/components/themeItem.vue";
  import HeaderNav from "@/components/headerNav";
  import Totop from '@/components/totop.vue';
  import FooterBottom from "@/components/footer";
  import InfiniteLoading from 'vue-infinite-loading';
  import {getThemeList} from '@/api/theme';
  import {Message} from "element-ui";

  export default {
    name: "themeList",
    components: {
      ThemeItem,
      HeaderNav,
      FooterBottom,
      InfiniteLoading,
      Totop
    },
    data() {
      return {
        themeList: [],
        activeName: '1',
        page: 0,
        editableTabs: [{
          title: '最新',
          name: '1',
        }, {
          title: '最热',
          name: '2',
        }],
        listpic: '?x-oss-process=style/list-img',
        userface: '?x-oss-process=style/user-img',
        detailpic: '?x-oss-process=style/detail-img',
        osshost: process.env.UPLOAD_URL,
      }
    },
    methods: {
      handleClick(tab, event) {
        this.activeName = tab.name;
        this.page = 0;
        this.themeList = [];
        this.infiniteHandler()
      },
      infiniteHandler($state) {
        this.page++;
        this.getList($state);
      },
      getList($state) {
        getThemeList(this.activeName, this.page).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length != 0) {
              this.themeList = this.themeList.concat(res.result); //追加新数据
              if (this.page != 0) {
                $state.loaded();
              }
            } else {
              $state.complete(); //这里是加载了10页数据，设置不在加载
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      navToDetail(id) {
        this.$router.push({path: "/themeDetail", query: {id: id}})
      },
      navToIndex(id) {
        this.$router.push({path: "/user", query: {id}})
      }
    }
  }
</script>

<style scoped>

</style>



